<template>
  <div class="search">
      <div class="search-box-wrapper">
          <SearchBox ref="searchBox" @query="onQueryChange"></SearchBox>
      </div>
      <div class="shortcut-wrapper" v-show="!query" ref="shortcutWrapper">
          <Scroll class="shortcut" ref="shortcut" :data="shortcut"　:refreshDelay="refreshDelay">
            <div>
              <div class="hot-key">
                  <h1 class="title">热门搜索</h1>
                  <ul>
                      <li class="item" v-for="item in hotKey" @click="addQuery(item.k)">
                          <span>{{item.k}}</span>
                      </li>
                  </ul>
              </div>
              <div class="search-history" v-show="searchHistory.length">
                   <h1 class="title">
                       <span class="text">搜索历史</span>
                       <span class="clear" @click="showConfirm">
                            <i class="icon-clear"></i>
                       </span>
                   </h1>
                   <SearchList :searches="searchHistory" @select="addQuery"
                               @delete="deleteOne"></SearchList>
                </div>
            </div>
          </Scroll>
      </div>
      <div class="search-result" v-show="query" ref="searchResult">
          <Suggest :query="query" @listScroll="blurInput" @select="saveSearch" ref="suggest"></Suggest>
      </div>
      <Confirm ref="confirm" text="是否清空所有搜索历史" confirmBtnText="清空"
               @confirm="clearHistory"></Confirm>
      <router-view></router-view>
  </div>
</template>


<script>
import SearchBox from 'base/search-box/search-box'
import Suggest from 'components/suggest/suggest'
import SearchList from 'base/search-list/search-list'
import Confirm from 'base/confirm/confirm'
import Scroll from 'base/scroll/scroll'
import {getHotKey} from 'api/search'
import {ERR_OK} from 'api/config'
import {mapActions,mapGetters} from 'vuex'
import {playListMixin,searchMixin} from 'common/js/mixin'

export default {
    mixins: [playListMixin,searchMixin],
    data() {
        return {
            hotKey: [],
            query: ''
        }
    },
    created() {
        this._getHotKey()
    },
    computed: {
        shortcut() {
            return this.hotKey.concat(this.searchHistory)
        },
        ...mapGetters([
            'searchHistory'
        ])
    },
    methods: {
        handlePlayList(playlist) {
            const bottom = playlist.length > 0 ? '60px' : ''

            this.$refs.searchResult.style.bottom = bottom
            this.$refs.suggest.refresh()

            this.$refs.shortcutWrapper.style.bottom = bottom
            this.$refs.shortcut.refresh()
        },
        //调用searchBox的setQuery接口
        addQuery(query) {
            this.$refs.searchBox.setQuery(query)
        },
        // onQueryChange(query) {
        //     //console.log(query)
        //     this.query = query
        // },
        // blurInput() {
        //     this.$refs.searchBox.blur()
        // },
        // saveSearch() {
        //     this.saveSearchHistory(this.query)
        // },
        deleteOne(item) {
            this.deleteSearchHistory(item)
        },
        clearHistory() {
            this.clearSearchHistory()
        },
        showConfirm() {
            this.$refs.confirm.show()
        },
        _getHotKey() {
          getHotKey().then((res) => {
            if (res.code === ERR_OK) {
              this.hotKey = res.data.hotkey.slice(0, 10)
              //console.log(this.hotKey)
            }
          })
        },
        ...mapActions([
            'saveSearchHistory',
            'deleteSearchHistory',
            'clearSearchHistory'
        ])
    },
    watch: {
        //假如搜索内容没有改变，跳转页面后刷新页面
        query(newQuery) {
            if (!newQuery) {
              setTimeout(() => {
                this.$refs.shortcut.refresh()
              }, 20)
            }
        }
    },
    components: {
        SearchBox,
        Suggest,
        SearchList,
        Confirm,
        Scroll
    }
}
</script>


<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .search
    .search-box-wrapper
      margin: 20px
    .shortcut-wrapper
      position: fixed
      top: 178px
      bottom: 0
      width: 100%
      .shortcut
        height: 100%
        overflow: hidden
        .hot-key
          margin: 0 20px 20px 20px
          .title
            margin-bottom: 20px
            font-size: $font-size-medium
            color: $color-text-l
          .item
            display: inline-block
            padding: 5px 10px
            margin: 0 20px 10px 0
            border-radius: 6px
            background: $color-highlight-background
            font-size: $font-size-medium
            color: $color-text-d
        .search-history
          position: relative
          margin: 0 20px
          .title
            display: flex
            align-items: center
            height: 40px
            font-size: $font-size-medium
            color: $color-text-l
            .text
              flex: 1
            .clear
              extend-click()
              .icon-clear
                font-size: $font-size-medium
                color: $color-text-d
    .search-result
      position: fixed
      width: 100%
      top: 178px
      bottom: 0
</style>


